*::-webkit-scrollbar {
  display: none !important;
}

.react-modal-sheet-container {
  /* custom styles */
  border-top-left-radius: 16px !important;
  border-top-right-radius: 16px !important;
}

.one-line {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
}

.showlog {
  position: absolute;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
}
.showlog .showlogContent {
  display: block;
  padding: 30px 0 15px 0;
  background: white;
}
.showlog .showlogContent .log-item {
  padding: 5px 10px;
}
.showlog .showlogContent .log-item.odd {
  background: #dbdbdb;
}
.showlog .showlogContent .log-item.colapse {
  height: 40px;
  overflow: hidden;
}
.showlog .showlogContent .log-item .itemLogTitle {
  overflow-wrap: break-word;
  padding-bottom: 5px;
  border-bottom: 1px solid #737373;
}
.showlog .showlogContent .log-item .itemLogContent {
  padding-top: 5px;
  font-size: 13px;
  overflow-wrap: break-word;
}
.showlog .showlogContent .log-item .itemLogContent pre {
  outline: 1px solid #ccc;
  padding: 5px;
  margin: 5px;
}
.showlog .showlogContent .log-item .itemLogContent .string {
  color: green;
}
.showlog .showlogContent .log-item .itemLogContent .number {
  color: darkorange;
}
.showlog .showlogContent .log-item .itemLogContent .boolean {
  color: blue;
}
.showlog .showlogContent .log-item .itemLogContent .null {
  color: magenta;
}
.showlog .showlogContent .log-item .itemLogContent .key {
  color: red;
}
.btnClear {
  position: fixed;
  z-index: 1000;
  bottom: 120px;
  right: 30px;
  background: blue;
  color: white;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  border-radius: 30px;
}
.showlogTool {
  position: fixed;
  z-index: 1000;
  bottom: 40px;
  right: 30px;
  background: #d0021b;
  color: white;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  border-radius: 30px;
}

.react-modal-sheet-backdrop {
  border: 0 !important;
  padding: 0px !important;
}

.mui-image-wrapper {
  background-color: transparent !important;
}

.MuiSwitch-root{
  margin:0 !important;
}